import React from 'react'
import Color from './presentation/color/color.js'

import './colorOrganizer.scss'

export default class ColorOrganizer extends React.Component {

  constructor (props) {
    super(props)
    this.onRate = this.onRate.bind(this)
  }

  onRate () {
    window.alert("you rated!")
  }

  render () {
    const color = { backgroundColor: "#333" }
    const title = "sample color"
    const starLimit = 5
    const starSelected = 3
    
    return (
    <div class="color-organizer-container">
      <div className="color-organizer-header">
        <p>add form will come here</p>
      </div>
      <div className="color-organizer-body u-flex--item-table">
        <Color color={color}
          title={title}
          starLimit={starLimit}
          starSelected={starSelected}
          onRate={this.onRate()} />
      </div>
    </div>
    )
  }
}



